// DONATION CONFIRMATION COMPONENT
// Accepts the inputs state, card token information, confirmDonation function,
// editDonation function and cancelDonation function.
const DonationConfirmation = ({
  inputs,
  token,
  confirmDonation,
  editDonation,
  cancelConfirmation,
}) => (
  <div id="donationConfirmation" >
    <Heading type='h2' text='Please Confirm Your Donation' />
    <div className="donationInfo">
      <div className="actions">
        <button
          onClick={editDonation}
          className="edit"
          type="button">
          <EditIcon className="icon" />
          <span className="text">Change Donation</span>
        </button>
        <button
          onClick={cancelConfirmation}
          className="cancel"
          type="button">
          <CloseIcon className="icon" />
          <span className="text">Cancel Donation</span>
        </button>
      </div>
      <div>
        <p className="title">
          Donation:
        </p>
        <p>{`$${(inputs.donation.slice(0, inputs.donation.length - 2))}.${inputs.donation.slice(-2)}`}</p>
        <p className="title">
          Payment Card:
        </p>
        <div className="cardInfo">
          <p>
            **** **** **** {token.card.last4}
          </p>
          {cardIcon(token.card.brand)}
        </div>
      </div>
      <Button
      primary
      type="button"
      onClick={confirmDonation}>
        Confirm Donation
      </Button>
    </div>
    <Button cancel onClick={cancelConfirmation} type="button">Cancel</Button>
  </div>
);

export default DonationConfirmation;



